* {
  box-sizing: border-box;
  font-family: 'BMHANNAPro';
  font-size: 20px;
  margin: 0;
  padding: 0;
}

:root {
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
  --default-text-color: rgb(255, 255, 255);
  height: fit-content;
  min-width: 500px;
  min-height: 1000px;
  max-height: fit-content;
}

main {
  display: block;
  min-width: 500px;
  padding: 25px;
  padding-bottom: 0;
  border-radius: 40px;
}

.listPart {
  display: block;
  min-width: 100%;
}
